* {
  padding: 0;
  margin: 0;
  background-color: rgb(41, 40, 40);
  font-family: 'Open Sans', sans-serif;
}

body {
  display: flex;
  justify-content: center;
}

main {
  width: 70vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

header {
  width: 90%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-bottom: 5%;
  margin-right: 8%;
}

header h2 {
  color: rgb(255, 166, 0);
  padding-left: 4%;
  padding-top: 3%;
}

header ul {
  width: 250px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

header ul li {list-style: none;}

header ul li a {outline-style: none;}

header ul li a button {
  padding: 2%;
  border-radius: 10px;
  height: 35px;
  width: 70px;
  color: white;
  border: 2px solid rgb(255, 166, 0)
}

header ul li a button:hover {
  border-color: white;
  transition: .3s ease;
}

.container-mid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.div-left {
  padding-left: 4%;
  width: 50%;
  display: flex;
  flex-direction: column;
}

.div-left h1 {
  font-size: 5vw;
  color: white;
}

.div-left h3 {
  font-size: 2vw;
  color: rgb(158, 158, 158);
}

.div-left ul {
  padding-top: 1%;
  padding-bottom: 1%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.div-left ul li{
  list-style: none;
}

.div-left p {
  width: 80%;
  font-size: 12px;
  color: rgb(128, 128, 128);
}

.btn-saiba {
  width: 80%;
  justify-self: center;
  text-align: center;
}

.btn-saiba button {
  margin-top: 5%;
  color: white;
  height: 40px;
  width: 100px;
  border: 2px solid rgb(255, 166, 0);
  border-radius: 10px;
}

.btn-saiba button:hover {
  border-color: white;
  transition: .3s ease;
}

.illustration {
  background-image: url('./img/ilustra√ßao.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 45%;
  height: 50vh;
  object-fit: contain;
}

footer {
  width: 100%;
  color: white;
  padding-top: 10%;
  text-align: center;
}

footer span {
  font-size: 10px;
  text-align: center;
}

@media (max-width:600px) {
  main{
    width: 100%;
  }
}

@media (max-width:400px) {
  main {
    align-items: center;
    height: 100%;
    width: 100%;
  }
  header {
    justify-content: center;
  }
  header ul{
    justify-content: center;
    width: 100%;
  }
  header h2 {
    font-size: 1.3rem;
    margin-top: 5%;
    padding-bottom: 5%;
  }
  .div-left {
    width: 100%;
  }
  .div-left h1 {
    font-size: 5vh;
  }
  .div-left h3 {
    font-size: 2vh;
  }
  .div-left p {
    width: 95%;
    padding-right: 2%;
  }
  .illustration {
    width: 100%;
    margin-top: 8%;
    margin-bottom: 8%;
  }
}